/* Base styles for all screen sizes */
body {
  border: 7px solid lightcoral;
  border-radius: 2px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}

h1 {
  text-align: center;
  color: black;
  font-weight: bold;
  border-bottom: 2px solid orangered;
  padding-bottom: 5px;
}

.container {
  background-color: rgba(255, 255, 255, 0.76);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 2px solid lightsalmon;
  padding: 25px;
  margin: 0 auto;
  width: 90%;
  height: 90%;
  max-width: 18rem;
  height: 18rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 0.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  overflow: hidden; /* Add this to prevent card overflow */
}

.card {
  border: 2px solid black;
  background-color: lightgoldenrodyellow;
  border-radius: 5px;
  animation: 2s hideCard linear;
  transform-style: preserve-3d;
  transition: transform 0.5s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.8);
  cursor: pointer; /* Add cursor pointer to indicate cards are clickable */
}

img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  opacity: 0;
  animation: 2s hideImage linear;
}

.card.active {
  transform: rotateY(0);
}

.card.active img {
  opacity: 1;
}

.unmatch {
  background-color: lightcoral;
  border-radius: 5px;
  animation: 2s hideCard linear;
}

/* Add a hover effect to the cards */
.card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

/* Media queries for mobile devices */
@media (max-width: 768px) {
  h1 {
    font-size: 1.5rem; /* Decrease font size for mobile */
  }

  .container {
    max-width: 20rem; /* Adjust maximum width for mobile */
    height: 23rem; /* Adjust height for mobile */
    grid-template-columns: repeat(4, 1fr); /* Use 4 columns for mobile */
    grid-template-rows: repeat(4, 1fr); /* Use 4 rows for mobile */
  }
}

.unmatched-count {
  border: 2px solid orangered;
  border-radius: 5px;
  padding: 1px 10px;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
  background-color: lightcyan;
}

/* Style for the restart button */
button {
  font-family: "Montserrat", sans-serif;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  border: 2px solid black;
}

button:hover {
  background-color: lightsalmon;
}

.container {
  margin-bottom: 20px;
}

p {
  text-align: justify;
}

.bottom {
  padding: 20px;
}
